<template>
    <div class="root">
        <PopupBlock v-if="isPopUpClosed" @CloseByWrapper="(state) => { isPopUpClosed = state }"
            @ClosePopUp="(state) => { isPopUpClosed = state }"></PopupBlock>
        <header class="header">
            <div class="container header__wrapper">
                <a href="/Zetflix/" class="header__title"><span class="bigLogo">NETFLIX</span><br>and chill</a>
                <div v-if="this.userAuthorize" class="header__authorize">
                    <div @click="isPopUpClosed = true" class="btn">Добавить фильмец</div>
                    <router-link to="/login-form" @click="clearLocalStorage" class="btn">Выйти</router-link>
                </div>
                <router-link v-else to="/login-form" class="header__enter btn">Войти</router-link>
            </div>
        </header>
    </div>
</template>

<script>
import PopupBlock from './popupBlock.vue';

export default {
    components: {
        PopupBlock
    },
    data() {
        return {
            userData: {

            },
            userAuthorize: false,
            isPopUpClosed: false,
        }
    },
    created() {
        if (localStorage.getItem("user")) {
            this.userAuthorize = true;
            this.userData = JSON.parse(localStorage.getItem("user"));

        }
    },
    methods: {
        clearLocalStorage() {
            localStorage.removeItem("user");
        }
    },
}
</script>

<style scoped>
.header {
    padding: 20px 0;
}

.header,
.header__wrapper {
    width: 100%;
    background-color: #003832;
}

.header__wrapper {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.header__authorize {
    display: flex;
    gap: 30px;
}

.header__title {
    position: relative;
    font-size: 24px;
    color: #fff;
    text-decoration: none;
}

.header__title::after {

    content: "";
    position: absolute;
    right: 0px;
    bottom: 0px;
    width: 32px;
    aspect-ratio: 1;
    background-image: url('../../assets/img/4x.png');
    background-size: cover;
}

.bigLogo {
    font-size: 36px;
    color: red;
    text-shadow: 2px 0px black, 0px 2px black, -2px 0px black, 0px -2px black;
}


@media (max-width:767.99px) {
    .btn {
        padding: 10px 20px;
        font-size: 16px;
    }

    .header__wrapper {
        flex-wrap: wrap;
        align-items: center;
        justify-content: center;
        gap: 30px;
    }

    .header__authorize {
        flex-wrap: wrap;
        align-items: center;
        justify-content: center;
        gap: 30px;
    }
}

@media (max-width:319.99px) {}
</style>