<template>
    <div class="root">
        <div class="control__panel" v-if="isAdmin">
            <ul class="control__list">
                <li class="control__list-controls btn" @click="deleteFilm()"><img src="../../assets/img/cross.svg"
                        alt="" class="controls__img">Удалить</li>
                <li class="control__list-controls btn" @click="inOrderFilm()"><img src="../../assets/img/camera.svg"
                        alt="" class="controls__img">
                    В очередь</li>
                <li class="control__list-controls btn" @click="fromOrderFilm()"><img src="../../assets/img/remove.svg"
                        alt="" class="controls__img">
                    В Предложенные</li>
                <li class="control__list-controls btn" @click="watchedFilm()"><img src="../../assets/img/done.svg"
                        alt="" class="controls__img">Просмотрено</li>
                <li class="control__list-controls btn" @click="editFilm()" v-if="this.film.status == 'Просмотрено'"><img
                        src="../../assets/img/pen.svg" alt="" class="controls__img">{{ isEdit ? "Сохранить" :
                            "Редактировать" }}</li>
            </ul>
        </div>
        <headerBlock></headerBlock>
        <div class="container">
            <div class="filmPage__wrapper">
                <div class="filmPage__img-wrapper">
                    <img :src="this.film.imgPath" alt="картинка фильма" class="filmPage__img">
                </div>
                <div class="filmPage__info-wrapper">
                    <h1 class="filmInfo__title">{{ this.film.filmName }}</h1>
                    <div class="filmInfo__year">{{ this.film.year }} - года</div>
                    <input readonly :value="this.film.desc" class="filmInfo__commentary"
                        placeholder="Короткий комментарий по фильму">
                </div>
                <div class="filmInfo__ratings">
                    <div class="filmInfo__ratings-text">Оценки зрителей:</div>
                    <div v-if="this.isEdit" class="filmInfo__ratings-add" @click="addNewRateField()"></div>
                    <div class="asd">
                        <div class="rating__wrapper" v-for="rank, index in Object.keys(this.onSiteRanks).length"
                            :key="index">
                            <input readonly type="text" :value="Object.keys(this.onSiteRanks)[index]"
                                class="rating__wrapper-input rating__wrapper-name" placeholder="Имя зрителя">
                            <input readonly type="text" :value="Object.values(this.onSiteRanks)[index]"
                                class="rating__wrapper-input  rating__wrapper-rate" placeholder="Оценка зрителя">
                            <div v-if="this.isEdit" class="rating__wrapper-deleteRate" @click="removeField(index)">
                                <img src="../../assets/img/cross.svg" alt="" class="controls__img">
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>


<script>

import headerBlock from '../blocks/headerBlock.vue';


export default {
    components: {
        headerBlock,
    },
    data() {
        return {
            film: "",
            isAdmin: false,
            isEdit: false,
            onSiteRanks: {},
            additionalField: 0,
        }
    },
    updated() {


    },
    methods: {
        removeField(index) {
            delete this.onSiteRanks[Object.keys(this.onSiteRanks)[index]];
        },
        addNewRateField() {
            this.additionalField++;
            let inputs = document.querySelectorAll(".rating__wrapper-input");
            let newObjForRanks = {};
            for (let i = 0; i < inputs.length; i = i + 2) {
                newObjForRanks[inputs[i].value] = Number(inputs[i + 1].value);
            }
            console.log(newObjForRanks)
            newObjForRanks[`Новое поле ${this.additionalField}`] = 0;
            console.log(newObjForRanks)
            this.onSiteRanks = newObjForRanks;
            setTimeout(() => {
                let inputs = document.querySelectorAll(".rating__wrapper-input");
                inputs.forEach((input) => {
                    input.removeAttribute("readonly")
                })
            }, 100)
        },
        async deleteFilm() {
            await fetch(`https://1d95bdc879b5963a.mokky.dev/films/${this.$route.params.id}`, {
                method: "DELETE",
                headers: { "content-type": "application/json" },
            })
            window.location = "/Zetflix/";

        },
        async inOrderFilm() {
            await fetch(`https://1d95bdc879b5963a.mokky.dev/films/${this.$route.params.id}`, {
                method: "PATCH",
                headers: { 'Content-type': 'application/json; charset=UTF-8', },
                body: JSON.stringify({
                    status: "В очереди",
                    timeOfWatch: "",
                    ranks: "",
                    desc: ""
                })
            })
            location.reload();
        },
        async fromOrderFilm() {
            await fetch(`https://1d95bdc879b5963a.mokky.dev/films/${this.$route.params.id}`, {
                method: "PATCH",
                headers: { 'Content-type': 'application/json; charset=UTF-8', },
                body: JSON.stringify({
                    status: "Предложенные",
                    timeOfWatch: "",

                })
            })
            location.reload();
        },
        async watchedFilm() {
            let buffer = new Date();
            let date = `${buffer.getDay()}.${buffer.getMonth() + 1}.${buffer.getFullYear()} - ${buffer.getHours()}:${buffer.getMinutes()}`
            await fetch(`https://1d95bdc879b5963a.mokky.dev/films/${this.$route.params.id}`, {
                method: "PATCH",
                headers: { 'Content-type': 'application/json; charset=UTF-8', },
                body: JSON.stringify({
                    status: "Просмотрено",
                    timeOfWatch: date,
                })
            })
            location.reload();
        },
        async editFilm() {
            const inputs = document.querySelectorAll(".rating__wrapper-input");
            const desc = document.querySelector(".filmInfo__commentary");
            if (!this.isEdit) {
                desc.removeAttribute("readonly")
                inputs.forEach((input) => {
                    input.removeAttribute("readonly")
                })
            } else {
                const objRate = {};
                for (let i = 0; i < inputs.length; i = i + 2) {
                    objRate[inputs[i].value] = Number(inputs[i + 1].value);
                }
                inputs.forEach((input) => {
                    input.setAttribute("readonly", true)
                })
                desc.setAttribute("readonly", true)
                console.log(objRate)
                await fetch(`https://1d95bdc879b5963a.mokky.dev/films/${this.$route.params.id}`, {
                    method: "PATCH",
                    headers: { 'Content-type': 'application/json; charset=UTF-8', },
                    body: JSON.stringify({
                        ranks: objRate,
                        desc: desc.value
                    })
                })

                location.reload();
            }
            this.isEdit = !this.isEdit;
        }
    },
    async created() {
        if (JSON.parse(localStorage.getItem("user")).role == "Админ") {
            this.isAdmin = true
        }
        let response = await fetch(`https://1d95bdc879b5963a.mokky.dev/films?id=${this.$route.params.id}`, {
            method: "GET",
            headers: { "content-type": "application/json" },
        })
        this.film = (await response.json())[0];

        if (this.film.ranks) {
            this.onSiteRanks = this.film.ranks;
        }
        else {
            this.onSiteRanks = {}
        }


    },

}

</script>

<style scoped>
.control__panel {
    z-index: 50;
    position: fixed;
    padding: 50px 20px;
    top: 50%;
    left: 20px;
    background: #003832;
    transform: translateY(-50%);
    border-radius: 0px 15px 15px 0px;
    width: 76px;
    transition: all .3s;
}

.control__list-controls,
.rating__wrapper-deleteRate {
    width: 36px;
    height: 36px;
    overflow: hidden;
    display: flex;
    align-items: center;
    gap: 10px;
    padding: 10px 10px;
    font-size: 14px;
    color: #fff;
    font-weight: 700;
    text-wrap: nowrap;
}

.control__list-controls:hover {
    width: 100%;
}

.control__panel:has(.control__list-controls:hover) {
    width: 220px;
}

.controls__img {
    width: 16px;
    aspect-ratio: 1;
}

.control__list-controls:not(:last-child) {
    margin-bottom: 20px;
}

.filmPage__wrapper {
    padding-top: 50px;
    display: flex;
    flex-wrap: wrap;
    gap: 50px;
}

.filmPage__img {
    display: inline-block;
    width: 320px;
    height: 100%;
}

.filmPage__info-wrapper {
    flex-grow: 1;
}

.filmPage__name {
    display: inline-block;
}

.filmInfo__title {
    font-size: 32px;
    font-weight: 700;
    color: #fff;
    margin-bottom: 10px;
}

.filmInfo__year {
    font-size: 20px;
    color: #fff;
    margin-bottom: 20px;
}

.filmInfo__commentary {
    font-size: 24px;
    font-style: italic;
    color: black;
    width: 100%;
    padding-bottom: 5px;
    background: transparent;
    border: none;
    outline: none;
    border-bottom: 2px solid transparent;
}

.filmInfo__ratings {
    position: relative;
    width: 100%;
}

.filmInfo__ratings-add {
    cursor: pointer;
    top: 0;
    left: 320px;
    position: absolute;
    background-color: #119686;
    border-radius: 50%;
    background-image: url("../../assets/img/cross.svg");
    background-size: contain;
    transform: rotate(45deg);
    width: 16px;
    aspect-ratio: 1;
}

.filmInfo__ratings-text {
    font-size: 32px;
    color: #fff;
    font-weight: 700;
    margin-bottom: 20px;
}

.rating__wrapper {
    display: flex;
    align-items: center;
    cursor: pointer;
}

.rating__wrapper:not(:last-child) {

    margin-bottom: 10px;
}

.rating__wrapper-input {
    padding: 10px 15px;
    border-radius: 20px;
    background: #F5F5F5;
    border: none;
    border: 3px solid #119686;
    outline: 2px solid transparent;
    transform-origin: center;
}

.rating__wrapper-input:not(:last-child) {
    margin-right: 20px;
}

.rating__wrapper-input:is(:not(:read-only)) {
    animation: shake .2s;
}

.filmInfo__commentary:is(:not(:read-only)) {
    border-bottom: 2px solid gray;
}


@keyframes shake {
    0% {
        transform: rotate(5deg);
    }

    100% {
        transform: rotate(-5deg);
    }
}
</style>