<template>
    <div class="popup__wrapper" @click="$emit('CloseByWrapper', wrapperCheck($event))">

        <div class="popup">
            <div class="popup__close" @click="$emit('ClosePopUp', false)">Х</div>
            <h2 class="popup__title">Впиши название фильма </h2>
            <div class="popup__top">
                <inputComponent class="popup__input" @keypress="checkKeyPress($event.key)"
                    @returnValue="(text) => this.filmName = text" :textForPlaceholder="'Название фильма'"
                    :error="this.errorText">
                </inputComponent>
                <div class="popup__btn" @click="findFilm()">
                    <img src="../../assets/img/search-svg.svg" alt="" class="popup__btn-img">
                </div>
            </div>
            <div v-if="this.listOfFindedFilms.length == 0" class="popup__attention">Данная параша работает по
                бесплатному API
                ключу, потому что маленькая инди
                компания Yandex не может позволить себе больше 200 бесплатных запросов(Привет ютубу, где я делал по
                1000+ в день).Так что динамического поиска тут нет, просьба писать название фильма без ошибок на всякий
                пожарный =)</div>
            <div v-else class="popup__films">
                <div class="popup__films-columns">
                    <addMovieCard v-for="item, index in this.listOfFindedFilms.docs"
                        @checkFilm="(index) => { this.indexOfFilms = index; this.isClicked = true }" :key="index"
                        :name="item.name" :index="index" :desc="item.description" :year="item.year">
                    </addMovieCard>
                </div>
                <div v-if="isClicked" class="popup__films-context">
                    <div class="popup__films-content">
                        <img :src="this.listOfFindedFilms.docs[indexOfFilms].poster.previewUrl"
                            alt="Картинка для фильма" class="popup__content-img">
                        <div class="popup__film-texts">
                            <div class="popup__context-title">{{ this.listOfFindedFilms.docs[indexOfFilms].name }}
                            </div>
                            <div class="popup__context-year">{{ this.listOfFindedFilms.docs[indexOfFilms].year }} - года
                            </div>
                            <div class="popup__context-genres">Жанры: {{ makeGenres() }}</div>
                            <div class="popup__context-description">{{
                                this.listOfFindedFilms.docs[indexOfFilms].description
                            }}
                            </div>
                        </div>
                    </div>
                    <div class="popup__context-btn btn" @click="addNewFilm()">Добавить</div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import addMovieCard from '../elements/addMovieCard.vue';
import inputComponent from '../elements/inputComponent.vue';
import { useContent } from '@/stores/filmsStore';
export default {

    data() {
        return {
            indexOfFilms: 0,
            isClicked: false,
            filmName: "",
            errorText: "",
            listOfFindedFilms: [],
            films: [],
        }
    },
    components: {
        inputComponent, addMovieCard
    },
    methods: {
        wrapperCheck(event) {

            if (event.target.classList.contains("popup__wrapper")) {
                console.log(event.target)
                return false;
            }
            else {
                return true;
            }
        },
        async findFilm() {
            if (this.filmName == "") {
                this.errorText = "Название фильма напиши даун)"
            }
            else {
                try {
                    this.errorText = ""
                    let response = await fetch(`https://api.kinopoisk.dev/v1.4/movie/search?page=1&limit=10&query=${this.filmName}`, {
                        method: "GET",
                        headers: {
                            accept: 'application/json',
                            'X-API-KEY': '0ZGKZ63-NXGMT2M-K6VBGQZ-4Y50733'
                        }
                    })
                    this.listOfFindedFilms = await response.json()
                }
                catch (error) {
                    console.log(error);
                }
            }
        },
        checkKeyPress(key) {
            console.log();
            if (key == "Enter") {
                this.findFilm();
            }
        },
        async addNewFilm() {
            try {
                await fetch("https://1d95bdc879b5963a.mokky.dev/films", {
                    method: "POST",
                    headers: {
                        'Content-Type': 'application/json;charset=utf-8'
                    },
                    body: JSON.stringify({
                        filmName: this.listOfFindedFilms.docs[this.indexOfFilms].name,
                        year: this.listOfFindedFilms.docs[this.indexOfFilms].year,
                        imgPath: this.listOfFindedFilms.docs[this.indexOfFilms].poster.previewUrl,
                        whoSendIt: JSON.parse(localStorage.getItem("user")).name,
                        status: "Предложенные",
                    })

                })
                this.films = useContent();
                this.films.getFilms();
            }
            catch (error) {
                console.log(error)
            }
        },
        makeGenres() {
            let genresString = "";
            for (let genre in this.listOfFindedFilms.docs[this.indexOfFilms].genres) {
                genresString += ` ${this.listOfFindedFilms.docs[this.indexOfFilms].genres[genre].name},`
            }
            return genresString.substring(0, genresString.length - 1);
        }
    }
}

</script>


<style scoped>
.popup__wrapper {
    position: fixed;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    background-color: rgba(0, 0, 0, 0.8);
    z-index: 5;
}

.popup {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 1260px;
    height: 800px;
    background: #003832;
    padding: 50px 20px;
    border-radius: 20px;
    z-index: 10;
}

.popup__close {
    position: absolute;
    top: 20px;
    right: 30px;
    font-size: 20px;
    color: #fff;
    font-weight: 700;
    cursor: pointer;
}

.popup__title {
    font-size: 32px;
    color: #fff;
    font-weight: 700;

    margin-bottom: 20px;
}

.popup__top {
    position: relative;
}


.popup__btn {
    position: absolute;
    top: 10px;
    right: 15px;
    display: flex;
    border-radius: 5px;
    justify-content: center;
    align-items: center;
    width: 24px;
    aspect-ratio: 1;
    cursor: pointer;
}

.popup__btn-img {
    width: 100%;
    height: 100%;
}

.popup__attention {
    font-size: 14px;
    color: #fff;
    text-align: center;
}

.popup__films {
    max-height: 600px;
    overflow-y: auto;
    display: flex;
    gap: 20px;
}

.popup__films-content {
    display: flex;
    gap: 20px;
    margin-bottom: 50px;
}

.popup__content-img {
    width: 320px;
    height: 480px;
}

.popup__films-columns {
    scrollbar-color: #119686 #07413A;
    scrollbar-width: thin;
    max-width: 360px;
    flex-shrink: 0;
    padding: 0 5px;
    overflow: hidden;
    overflow-y: auto;
}

.popup__films-context {
    width: 100%;
    padding: 0 20px;
    color: #fff;
}

.popup__context-title {
    font-size: 32px;
    font-weight: 700;
    margin-bottom: 10px;
}

.popup__context-year {
    font-size: 14px;
    margin-bottom: 20px;
}

.popup__context-genres {
    font-size: 20px;
    margin-bottom: 50px;
}

.popup__context-description {
    font-size: 20px;
    margin-bottom: 40px;
}
</style>